<template>
<div class="Contact">
    <h2>Contactez-nous</h2>
    <div class="container">
        <form>
          <label>Name</label>
          <input 
            type="text" 
            v-model="name"
            name="name"
            placeholder="Your Name"
          >
          <label>Email</label>
          <input 
            type="email" 
            v-model="email"
            name="email"
            placeholder="Your Email"
            >
          <label>Message</label>
          <textarea 
            name="message"
            v-model="message"
            cols="30" rows="5"
            placeholder="Message">
          </textarea>
          
          <input type="submit" value="Send">
        </form>
    </div>

</div>
    
</template>

<script>

    import emailjs from 'emailjs-com';

    export default {
    name: 'ContactUs',
    data() {
        return {
        name: '',
        email: '',
        message: ''
        }
    },
    methods: {
        sendEmail(e) {
        try {
            emailjs.sendForm('service_ghmd05f', 'template_7v3nfl4', e.target,
            '72lEXY0ZsZQV2Wf_1', {
            name: this.name,
            email: this.email,
            message: this.message
            })

        } catch(error) {
            console.log({error})
        }
        // Reset form field
        this.name = ''
        this.email = ''
        this.message = ''
        },
    }
    }
</script>

<style scoped>
Label{
    color: white;
}
* {box-sizing: border-box;}

.container {
  display: block;
  margin:auto;
  text-align: center;
  border-radius: 10px 0 10px 0;
  background-color: #264346;
  padding: 20px;
  width: 50%;
}

